/**
 *   
 */
function getUserInfo () {
  let userInfo = localStorage.getItem('userInfo');
  if (userInfo !== null) {
    userInfo = JSON.parse(userInfo);
    // TODO : + 
    return userInfo;
  } else {
    return false;
  }
}

function validationCheck (type, value, thisObj, allowEmpty) {
  allowEmpty = allowEmpty === undefined ? false : allowEmpty;
  let result = false;
  if (type === 'EMAIL') {
    // eslint-disable-next-line no-useless-escape
    const regExp = /^[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-Za-z0-9\-]+/;
    if (allowEmpty === true) {
      if (value.length === 0) {
        thisObj.inputClass.id = '';
        thisObj.errorMessage.id = '';
        result = true;
      } else {
        if (value.length === 0) {
          thisObj.inputClass.id = '';
          thisObj.errorMessage.id = '(ID) ';
        }
      }
    }
    if (result === false) {
      if (value.length < 7) {
        thisObj.inputClass.id = 'error';
        thisObj.errorMessage.id = '(ID) 7  ';
      } else if (regExp.test(value) === false) {
        thisObj.inputClass.id = 'error';
        thisObj.errorMessage.id = '(ID)  ';
      } else {
        thisObj.inputClass.id = 'active';
        thisObj.errorMessage.id = '';
        result = true;
      }
    }
  } else if (type === 'PASSWORD') {
    const num = /[0-9]/g;
    const eng = /[a-z]/ig;
    const spe = /[`~!@@#$%^&*|'";:/?]/gi;
    if (allowEmpty === true) {
      if (value.length === 0) {
        thisObj.inputClass.password = '';
        thisObj.errorMessage.password = '';
        result = true;
      }
    } else {
      if (value.length === 0) {
        thisObj.inputClass.password = 'error';
        thisObj.errorMessage.password = ' ';
      }
    }
    if (result === false) {
      if (value.length < 8 || value.length > 20) {
        //   8~20  
        thisObj.inputClass.password = 'error';
        thisObj.errorMessage.password = '  8~20 ';
      } else if (value.search(/\s/) !== -1) {
        //   
        thisObj.inputClass.password = 'error';
        thisObj.errorMessage.password = '  ';
      } else if (value.search(num) < 0 || value.search(eng) < 0 || value.search(spe) < 0) {
        //  , ,   
        thisObj.inputClass.password = 'error';
        thisObj.errorMessage.password = ' , ,   ';
      } else {
        thisObj.inputClass.password = 'active';
        thisObj.errorMessage.password = '';
        result = true;
      }
    }
  } else if (type === 'TEXT') {
    if (allowEmpty === true) {
      if (value.length === 0) {
        thisObj.inputClass.name = '';
        thisObj.errorMessage.name = '';
        result = true;
      }
    } else {
      if (value.length === 0) {
        thisObj.inputClass.name = 'error';
        thisObj.errorMessage.name = '  ';
      } else {
        thisObj.inputClass.name = 'active';
        thisObj.errorMessage.name = '';
        result = true;
      }
    }
  }
  return result;
}

export {
  getUserInfo,
  validationCheck
};
